.currentPage {
    font-weight: bold;
    color: green;
}

.pagination {
    display: flex;
    list-style-type: none;
    justify-content: center;
    width: 100%;
    align-items: center;
}
.pagination li {
    margin-left: 3px;
    margin-right: 3px;
}

.pagination .disabled {
    opacity: .3;
    cursor: default;
}
.pagination .active a {
    border-color: orange;
    font-weight: bold;
}
.pagination .disabled a {
    cursor: default;
}
.pagination li a {
    cursor: pointer;
    display: block;
    border: 1px solid #282c34;
    padding: 5px 10px;
    outline: none
}